import { useAccountStore } from '@cetus/stores'
import useTokenBalanceStore from '@cetus/stores/src/tokenBalance'
import { BalanceInfo, Token } from '@cetus/types/src/common-types'
import { extractStructTagFromType } from '@cetusprotocol/common-sdk'
import Decimal from 'decimal.js'
import { useMemo } from 'react'
import { useAccountBalance } from '.'
import { formatNumber, fromDecimalsAmountFix } from '../../utils/src/formatter'

export function useGetTokenBalance(coin?: Token) {
  const { tokenBalanceObj } = useTokenBalanceStore()
  const { currentAccount } = useAccountStore()
  const { fetchCoinBalance } = useAccountBalance()

  const getBalanceInfoFromCache = (token?: Token): BalanceInfo | undefined => {
    if (token) {
      const info = tokenBalanceObj[extractStructTagFromType(token.coin_type).full_address]
      console.log('ðŸš€ðŸš€ðŸš€ ~ useTokenBalance.ts:18 ~ useGetTokenBalance ~ tokenBalanceObj:', tokenBalanceObj)

      if (info) {
        const totalBalance = info.totalBalance
        const balanceFormat = fromDecimalsAmountFix(totalBalance, token.decimals).toString()
        return {
          balance: info.totalBalance,
          balanceFormat,
          balanceDisplay: formatNumber(balanceFormat, undefined, undefined, Decimal.ROUND_DOWN).toString()
        }
      }
      return undefined
    }

    return undefined
  }

  const balanceInfo = useMemo((): BalanceInfo | undefined => {
    return getBalanceInfoFromCache(coin)
  }, [coin?.coin_type, tokenBalanceObj, currentAccount?.address])

  return {
    balanceInfo,
    getBalanceInfoFromCache,
    fetchCoinBalance
  }
}
